<template>
  <div class="upload-images">

      <div @click="OnUpload">
<img src=""  class="avatar" >
<i class="el-icon-plus avatar-uploader-icon"></i>
      </div>
      <!--Dialog 对话框 -->
      <el-dialog
  title="请选择封面图片"
  :visible.sync="centerDialogVisible"
  width="50%"
  center>
  <!-- tab栏 -->
   <el-tabs v-model="activeName" >
    <el-tab-pane label="素材库" name="first">
 <!-- Radio单选框 -->
    <el-radio-group v-model="activeImage" @change="loadimages">
      <el-radio-button label="全部">全部</el-radio-button>
      <el-radio-button label="收藏">收藏</el-radio-button>
    </el-radio-group>
    <!-- 表格 -->
    <el-row :gutter='20'>
        <el-col :span='6' v-for="item in images" :key='item.id'>
            <img :src="item.url" alt="" height="200px">
        </el-col>
    </el-row>
    </el-tab-pane>

    <el-tab-pane label="上传图片" name="second">上传图片</el-tab-pane>
  </el-tabs>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      centerDialogVisible: false,
      activeName: 'first',
      activeImage: '全部',
      images: []
    }
  },
  methods: {
    // 点击出现对话框并渲染列表
    OnUpload () {
      this.centerDialogVisible = true
      this.loadimages()
    },
    // 获取素材图片并渲染列表得方法
    loadimages () {
      this.$axios({
        url: '/user/images',
        method: 'GET',
        params: {
          collect: this.activeImage === '收藏'
        }
      }).then(result => {
        console.log('图片素材请求成功', result.data)
        this.images = result.data.data.results
      }).catch(err => {
        console.log(err, '请求失败')
      })
    }
  },
  created () {

  }
}
</script>

<style lang="less" scoped>
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
   border: 1px solid #409EFF;
  }
</style>
