<template>
  <div class="userinfo">
      <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="用户头像">
    <el-upload
  class="avatar-uploader"
  action="https://jsonplaceholder.typicode.com/posts/"
  :http-request='upload'
  >
  <img width="100px" :src="form.photo">
</el-upload>
  </el-form-item>
  <el-form-item label="用户昵称">
     <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="个人介绍">
   <el-input v-model="form.intro"></el-input>
  </el-form-item>
  <el-form-item label="手机号">
   <el-input v-model="form.mobile"></el-input>
  </el-form-item>
  <el-form-item label="用户邮箱">
     <el-input v-model="form.email"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">保存修改</el-button>
  </el-form-item>
</el-form>
  </div>
</template>

<script>
import eventBus from '@/utils/event_bus.js'
export default {
  data () {
    return {
      form: {
        name: '',
        intro: '',
        email: '',
        photo: '',
        mobile: ''
      }
    }
  },
  methods: {
    // 点击修改用户信息
    onSubmit () {
      this.$axios({
        url: '/user/profile',
        method: 'PATCH',
        data: this.form
      }).then(result => {
        console.log('修改成功', result.data)
        this.getuserinfo()
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 修改用户头像
    upload (config) {
      console.log(config)
      const fd = new FormData()
      fd.append('photo', config.file)
      this.$axios({
        url: '/user/photo',
        method: 'PATCH',
        data: fd
      }).then(result => {
        console.log('修改头像成功')
        this.getuserinfo()
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 渲染用户信息
    getuserinfo () {
      this.$axios({
        url: '/user/profile',
        method: 'get'
      }).then(result => {
        console.log('请求成功')
        console.log(result.data)
        this.form = result.data.data
        eventBus.$emit('updata_user', this.form)
      }).catch(err => {
        console.log(err, '请求失败')
      })
    }
  },
  created () {
    this.getuserinfo()
  }
}
</script>

