<template>
  <div class="push">
    <el-card>
      <div slot="header" class="clearfix">
        <span>发布文章</span>
      </div>
      <!-- form表单 -->
      <el-form ref="form" :model="articleForm" label-width="80px">
        <el-form-item label="标题">
          <el-input v-model="articleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="内容">
          <quill-editor v-model="articleForm.content" ref="myQuillEditor" :options="editorOption"></quill-editor>
        </el-form-item>
        <el-form-item label="频道">
          <el-select v-model="articleForm.channel_id" placeholder="请选择活动区域">
            <el-option label="全部频道" :value="null"></el-option>
            <el-option :label="item.name" :value="item.id" v-for="item in channel" :key="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="封面">
          <el-radio-group v-model="articleForm.cover.type">
            <el-radio :label="-1">自动</el-radio>
            <el-radio :label="0">无图</el-radio>
            <el-radio :label="1">1张</el-radio>
            <el-radio :label="3">3张</el-radio>
          </el-radio-group>
          <!-- 封装的上传图片组件 -->
          <template v-if="articleForm.cover.type >= 0">
            <el-row :gutter="20">
              <el-col :span="4" v-for="item in articleForm.cover.type" :key="item">
                <UploadImages></UploadImages>
              </el-col>
            </el-row>
          </template>
        </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit(false)">发布</el-button>
    <el-button  @click="onSubmit(true)">存入草稿</el-button>
  </el-form-item>
</el-form>
</el-card>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import UploadImages from './components/upload-images'
export default {
  name: 'push',
  components: {
    // eslint-disable-next-line vue/no-unused-components
    quillEditor,
    UploadImages
  },
  data () {
    return {
      articleForm: {
        title: '',
        content: '',
        channel_id: '',
        cover: {
          type: 0,
          images: []
        }
      },
      channel: [],
      editorOption: {}
    }
  },
  methods: {
    get_channel () {
      this.$axios({
        url: '/channels',
        method: 'get'
      })
        .then(result => {
          this.channel = result.data.data.channels
        })
        .catch(err => {
          console.log(err, '请求失败')
        })
    },
    onSubmit (draft) {
      this.$axios({
        url: '/articles',
        method: 'post',
        headers: {
          Authorization: `Bearer ${window.localStorage.getItem('user-token')}`
        },
        params: {
          draft
        },
        data: this.articleForm
      })
        .then(result => {
          console.log('发布成功')
        })
        .catch(err => {
          console.log(err, '发布失败')
        })
    },
    // 获取指定文章
    get_article () {
      this.$axios({
        url: `/articles/${this.$route.params.articleid}`,
        method: 'get'
      })
        .then(result => {
          console.log(result.data, '请求成功')
          this.articleForm = result.data.data
        })
        .catch(err => {
          console.log(err, '请求失败')
        })
    }
  },
  created () {
    this.get_channel()
    this.get_article()
  }
}
</script>

