<template>
  <div class="caozuo">
      <el-card class="box-card">
  <div slot="header" class="clearfix">
    <span>评论内容列表</span>
  </div>
<!-- 表格 -->
<el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="date"
        label="头像"
        width="180">
        <template slot-scope="scope">
            <img :src="scope.row.aut_photo" alt="" width="50px">
        </template>
      </el-table-column>
      <el-table-column
        prop="content"
        label="评论内容"
        width="180">
      </el-table-column>
      <el-table-column
        prop="is_liking"
        label="点赞">
      </el-table-column>
       <el-table-column
        prop="like_count"
        label="点赞数量">
      </el-table-column>
       <el-table-column
        prop="pubdate"
        label="评论日期">
        <template slot-scope="scope">
            {{scope.row.pubdate|capitalize}}
        </template>
      </el-table-column>
       <el-table-column
        prop="reply_count"
        label="回复数量">
      </el-table-column>
       <el-table-column
        prop=""
        label="是否推荐">
        <template slot-scope="scope">
<el-switch
  v-model="scope.row.is_top"
  active-color="#13ce66"
  inactive-color="#ff4949"
  @change="onchange(scope.row)"
  >
</el-switch>
        </template>
      </el-table-column>
    </el-table>
</el-card>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  filters: {
    capitalize: function (value) {
      return moment(value).format('YYYY-MM-DD')
    }
  },
  props: {
    articleid: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      tableData: []
    }
  },
  methods: {
    loadcomment () {
      this.$axios({
        url: '/comments',
        method: 'GET',
        params: {
          type: 'a',
          source: this.articleid
        },
        data: {
          allow_comment: true
        }
      }).then(result => {
        console.log(result.data)
        this.tableData = result.data.data.results
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    onchange (comment) {
      this.$axios({
        url: `/comments/${comment.com_id}/sticky`,
        method: 'PUT',
        data: {
          sticky: comment.is_top
        }
      }).then(result => {
        console.log('置顶或取消成功')
      }).catch(err => {
        console.log(err, '请求失败')
      })
    }
  },
  created () {
    this.loadcomment()
  }

}
</script>

