<template>
  <div class="comment">
<el-card class="box-card">
  <div slot="header" class="clearfix">
    <span>评论列表</span>
    <!-- 表格 -->
     <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="title"
        label="标题"
        width="360">
      </el-table-column>
      <el-table-column
        prop="comment_status"
        label="评论状态"
        width="180">
        <template slot-scope="scope">
          <el-switch
          v-model="scope.row.comment_status"
          active-color="#13ce66"
          inactive-color="#ff4949"
          @change="onchangestatus(scope.row)"
          >
</el-switch>
        </template>
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label="总评论数">
      </el-table-column>
       <el-table-column
        prop="fans_comment_count"
        label="粉丝评论数">
      </el-table-column>
       <el-table-column
        prop="address"
        label="操作">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" circle @click="$router.push('/totalcomment/'+scope.row.id)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle @click="On_delete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</el-card>
  </div>
</template>

<script>
export default {
  name: 'aaa',
  data () {
    return {
      tableData: []

    }
  },
  methods: {
    // 获取评论
    getcomment () {
      this.$axios({
        url: '/articles',
        method: 'get',
        params: {
          response_type: 'comment'
        }
      }).then(result => {
        console.log(result.data, '请求成功')
        this.tableData = result.data.data.results
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 修改评论状态
    onchangestatus (article) {
      this.$axios({
        url: '/comments/status',
        method: 'PUT',
        params: {
          article_id: article.id.toString()
        },
        data: {
          allow_comment: article.comment_status
        }
      }).then(result => {
        console.log('启用或关闭成功')
        this.$message({
          type: 'success',
          message: `${article.comment_status ? '启用' : '关闭'}`
        })
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 删除评论
    On_delete (commentId) {
      this.$axios({
        url: `/comments/${commentId}`,
        method: 'DELETE'
      }).then(result => {
        console.log('删除成功！')
      }).catch(err => {
        console.log(err, '请求失败！')
      })
    }
  },
  created () {
    this.getcomment()
  }
}
</script>

