<template>
  <div class="material">
    <el-card class="box-card">
  <div slot="header" class="clearfix">
    <span>图片管理</span>
  </div>
  <el-upload
  style="float:right"
  class="upload-demo"
  action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
  :headers='uploadheader'
  name='image'
  :on-success='on_upload'
  :show-file-list='false'
  >
  <el-button size="small" type="primary">点击上传</el-button>
</el-upload>
<el-button size="small" type="success" style="float:right;margin-right: 20px" @click="OnUpload">手动上传图片</el-button>
   <div>
    <el-radio-group v-model="value" @change="on_find">
      <el-radio-button label="全部"></el-radio-button>
      <el-radio-button label="收藏"></el-radio-button>
       <input type="file" hidden ref="input" @change="OnChange">
    </el-radio-group>
  </div>
  <el-row :gutter="20">
  <el-col :xs='24' :sm='12' :md='6' :lg='4' v-for="item in carddata" :key='item.id' class="itemcol">
    <el-card :body-style="{ padding: '20px' }">
      <img :src="item.url" class="image" height="200px">
      <div style="padding: 14px;" class="action">
  <i :class="item.is_collected? 'el-icon-star-on':'el-icon-star-off'" @click="onshou_cang(item)"></i>
  <i class="el-icon-delete-solid" @click="on_delete(item.id)"></i>
        <div class="bottom clearfix">
        </div>
      </div>
    </el-card>
  </el-col>

</el-row>

</el-card>
  </div>
</template>

<script>
const token = window.localStorage.getItem('user-token')
export default {
  name: 'aaa',
  data () {
    return {
      carddata: [],
      value: '',
      uploadheader: {
        Authorization: `Bearer ${token}`
      }
    }
  },
  methods: {
    // 点击获取全部或者收藏图片
    on_find (value) {
      this.getimage(value !== '全部')
    },
    // 获取素材图片
    getimage (iscollected = false) {
      this.$axios({
        url: '/user/images',
        method: 'get',
        params: {
          collect: iscollected
        }
      }).then(result => {
        console.log('请求成功了哟')
        console.log(result.data)
        this.carddata = result.data.data.results
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 添加或取消收藏
    onshou_cang (images) {
      this.$axios({
        url: `/user/images/${images.id}`,
        method: 'PUT',
        data: {
          collect: !images.is_collected
        }
      }).then(result => {
        console.log('请求成功')
        images.is_collected = !images.is_collected
      })
    },
    // 删除图片
    on_delete (imgid) {
      this.$axios({
        url: `/user/images/${imgid}`,
        method: 'delete'
      }).then(result => {
        console.log('删除成功')
        this.getimage()
      }).catch(err => {
        console.log(err, '请求失败')
      })
    },
    // 上传图片后加载素材列表
    on_upload () {
      this.getimage()
    },
    // 手动上传图片
    OnUpload () {
      this.$refs.input.click()
    },
    OnChange () {
      // 获取用户上传文件对象
      const fileobj = this.$refs.input.files[0]
      const fd = new FormData()
      fd.append('image', fileobj)
      this.$axios({
        url: '/user/images',
        method: 'post',
        data: fd
      }).then(result => {
        console.log('请求成功')
      }).catch(err => {
        console.log(err, '请求失败')
      })
    }
  },
  // 组件初始化完成后渲染列表
  created () {
    this.getimage()
  }
}
</script>

<style lang="less" scoped>
.itemcol{
  margin-bottom: 20px;
  .action{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
  }
}
</style>
